import React, { Component } from "react";
import "./createpost.css";

class CreatePost extends Component {
  state = {
    Heading: "Enter Heading of the Post",
    Post_Content: "Type Your Post Here",
    Author: this.props.id,
    productImage: null,
    message:""
  };

  handlesubmit = (e) => {
    e.preventDefault();

    const form1 = new FormData();
    form1.append("Heading", this.state.Heading);
    form1.append("Post_Content", this.state.Post_Content);
    form1.append("Author", this.state.Author);
    form1.append("productImage", this.state.productImage);
    
    fetch("/create/createpost", {
      method: "POST",
      body: form1,
    }).then(res => res.json()).then(data=>{this.setState({message:data.message})})
    .catch(err=>{console.log(err)});
  };  

  handlechange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
      message:""
    });   
  };
  
  handlechangeimage = (e) => {
    this.setState({
      [e.target.name]: e.target.files[0],
    });
  };

  render() {
    return (
      <div >
        
          <form name='newform' className='createpost' onSubmit={this.handlesubmit}
            encType='multipart/form-data'> 
            <div className="createpost_heading">
              <input
                      type='text'
                      name='Heading'
                      value={this.state.Heading}
                      onChange={this.handlechange}
                    />
            </div>
            <div>
              <textarea
                      name='Post_Content'
                      value={this.state.Post_Content}
                      onChange={this.handlechange}
                    />
            </div>
            <div>
              <input
                  type='file'
                  name='productImage'
                  onChange={this.handlechangeimage}
                />
            </div>

            <p>{this.state.message}</p>
            <div>
              <button>Submit</button>
            </div>

          </form>
      </div>
    );
  }
}

export default CreatePost;
