import React, { Component } from "react";
import "./importantpost.css";

import { Link } from "react-router-dom";

class Importantpost extends Component {
  state = {
    Impost: [],
    current_id: this.props.id,
  };

  componentDidMount() {
    console.log(this.state.current_id);
    fetch("/post/imppost", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "same-origin",
      body: JSON.stringify({
        id: this.state.current_id,
      }),
    }).then(res=>res.json()).then(data=>{
      this.setState({Impost:data})
    })
  }

  render() {
    const Imposts = this.state.Impost.map((post) => {
      return (
        <div className='ImpPost'>
          <div className="ImpPost_blank">
          </div>
          <div className="ImpPost_image">
            <img src={`http://bain-box.com/${post.productImage}`} />
          </div>
          <div className="ImpPost_body">
              <div className="ImpPost_body_upper_header">            
                  <div>
                   by Ankit / Published on: {post.Published_Date}
                  </div>
                  <div>
                  <button>{post.following_status}</button>
                  </div>
                  
              </div>
                
               <div className="ImpPost_body_heading">
                  <Link to={{ pathname: `/post/${post._id}` }}>{post.Heading}</Link>
               </div>
                
                <div>
                  <p>{post.Post_Content}</p>
                  <p>Read More</p>
                </div>
            </div>
              
        </div>
      );
    });

    return <div>{Imposts}</div>;
  }
}

export default Importantpost;
