import React, { Component } from "react";
import "./mypost.css";
import { Link, Route, BrowserRouter } from "react-router-dom";

class MyPost extends Component {
  state = {
    current_id: this.props.id,
    mypost: [],
  };

  handleDelete = (post_id) => {
    fetch("/create/deletepost", {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "same-origin",
      body: JSON.stringify({
        id: this.state.current_id,
        post_id: post_id,
      }),
    }).then(
      fetch("/post/mypost", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "same-origin",
        body: JSON.stringify({
          id: this.state.current_id,
        }),
      })
        .then((res) => res.json())
        .then((data) => this.setState({ mypost: data })).catch(err=>{console.log(err)})
    );
  };

  componentDidMount() {
    fetch("/post/mypost", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "same-origin",
      body: JSON.stringify({
        id: this.state.current_id,
      }),
    })
      .then((res) => res.json())
      .then((data) => this.setState({ mypost: data })).catch(err=>{console.log(err)});
  }
  render() {
    const myposts = this.state.mypost.map((post) => {
      return (
        <div className='mypost'>
          <div className="mypost_blank"></div>
          <div className='mypost_image'>
            <img src={`http://bain-box.com/${post.productImage}`} />
          </div>
          <div className='mypost_body'>
                <div className="mypost_body_upper_header">
                  <div className="mypost_body_upper_header_date">
                    Published On: {post.Published_Date}
                  </div>
                  <div className="mypost_body_upper_header_edit_delete">
                    <Link
                      to={{
                        pathname: "/editpost",
                        state: {
                          Heading: post.Heading,
                          Post_Content: post.Post_Content,
                          Post_id: post._id,
                        },
                      }}>
                      Edit
                    </Link>
                    
                  <button onClick={() => this.handleDelete(post._id)}>
                    Delete
                  </button>
                  </div>
                  
                </div>
              
              <div className="mypost_body_heading">
                <Link to={{ pathname: `/post/${post._id}` }}>
                  {post.Heading}
                </Link>
              </div>
              <div className="mypost_body_main_content">
                <p>{post.Post_Content}</p>
                <div>Read More</div>
              </div>
              
            </div>
        </div>
      );
    });
    return <div>{myposts}</div>;
  }
}

export default MyPost;
