import React, { Component } from "react";
import { Link, Route, BrowserRouter } from "react-router-dom";
import "./register.css";

class Register extends Component {
  state = {
    name: "",
    age: "",
    username: "",
    password: "",
    message:""
  };
  handleregister = (e) => {
    e.preventDefault();

      fetch("/user/register", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          name:this.state.name,
          age:this.state.age,
          username: this.state.username,
          password: this.state.password,
        }),
        credentials: "same-origin",
      })
        .then((res) => res.json())
        .then((data) => {
          this.setState({message:data.message}) 
        }).catch(err=>{console.log(err)});
      };

  handlechange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
      message:""
    });
  };

  render() {
    return (
      <div className='register'>
         <div className="register_navbar" >
            <div className="register_logo">
            <Link to='/'> Mugu</Link>            
            </div>
            <div className="register_login_register">
              <Link to='/login'> Login</Link>
              <Link to='/register'> Register</Link>
            </div>  
          </div>


          <h2 className='register_text'>Register</h2>
          <p style={{color:"red", marginLeft:"43.5%"}}>{this.state.message}</p>
          <form className='register_form' onSubmit={this.handleregister}>
            <div className="register_form_name_age">
            <input
                type='text'
                name='name'
                placeholder='Enter your Name'
                onChange={this.handlechange}
              />
              <input
                type='text'
                name='age'
                placeholder='Enter your Age'
                onChange={this.handlechange}
              />
            </div>
            <div className="register_form_username_password">
            <input
                type='text'
                name='username'
                placeholder='Enter your Username'
                onChange={this.handlechange}
              />
              <input
                type='text'
                name='password'
                placeholder='Enter your password'
                onChange={this.handlechange}
              />
            </div>
            <div className="register_form_submit">
                <button >Submit</button>
            </div>
              
           </form>


          <div className='already_registered'>
            <div>
              If already Registered <Link to='/login'>Click Here</Link> to Login
            </div>
          </div>

      </div>
    );
  }
}

export default Register;
