import React, { Component } from "react";
import "./allposts.css";

import { Link } from "react-router-dom";

class Allpost extends Component {
  state = {
    current_id: this.props.id,
    allpost: [],
  };
  componentDidMount() {
    fetch("/post/allpost", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "same-origin",
      body: JSON.stringify({
        id: this.state.current_id,
      }),
    })
      .then((res) => res.json())
      .then((data) => this.setState({ allpost: data })).catch(err=>{console.log(err)});
  }

  handlefollow = (post_author, following_status) => {
    if (following_status == "Follow") {
      fetch("/connection/follow", {
        method: "POST",
        headers: {
          "content-Type": "application/json",
        },
        credentials: "same-origin",
        body: JSON.stringify({
          req_from: this.state.current_id,
          req_to: post_author,
        }),
      }).then(fetch("/post/allpost", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "same-origin",
        body: JSON.stringify({
          id: this.state.current_id,
        }),
      })
        .then((res) => res.json())
        .then((data) => this.setState({ allpost: data })));  
    } else{
      fetch("/connection/unfollow", {
        method: "POST",
        headers: {
          "content-Type": "application/json",
        },
        credentials: "same-origin",
        body: JSON.stringify({
          req_from: this.state.current_id,
          req_to: post_author,
        }),
      }).then(fetch("/post/allpost", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "same-origin",
        body: JSON.stringify({
          id: this.state.current_id,
        }),
      })
        .then((res) => res.json())
        .then((data) => this.setState({ allpost: data }))).catch(err=>{console.log(err)});
    }
    
  };

  render() {
    const allpost = this.state.allpost.map((post) => {
      return (
        <div className='allpost'>
          <div className="allpost_empty"></div>  
          <div className='allpost_image'>
            <img src={`http://bain-box.com/${post.productImage}`} />
          </div>
          <div className="allpost_content">

            <div className='allpost_content_author_section'>
                by Ankit / {post.Published_Date}
                <button onClick={() => this.handlefollow(post.Author, post.following_status)}>
                  {post.following_status}
                </button>
            </div>
            
            <div className='allpost_content_heading'>
              <Link to={{ pathname: `/post/${post._id}` }}>{post.Heading}</Link>
            </div>
            
            <div className='allpost_content_body'>
              <p>{post.Post_Content}</p>
              <Link to={{ pathname: `/post/${post._id}` }}>Read More</Link>
            </div>

          </div>

        </div>
      );
    });

    return (
      <div>
        <div> {allpost} </div>
      </div>
    );
  }
}

export default Allpost;
