import React, { Component } from "react";
import "./home.css";
import { Link, Route, BrowserRouter } from "react-router-dom";

class Home extends Component {
  render() {
    return (
      <div className='home'>
        <div className="home_content">
        <h1>Stories That Moves With You</h1>
        <p>Find pieces by independent writers from around the globe, 
          stories we feature and leading authors, and smart takes on 
          our own suite of blogs and publications
        </p>
        </div>
        <div className="home_empty"></div>
      </div>
    );
  }
}

export default Home;
