import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./navbar.css";
import menu_icon from './icons/square.svg'

class Navbar extends Component {
  state = {
    display: "none",
    width:"0",
  };

  handlehover = (e) => {
    this.setState({ display: "block" });
  };

  handlehover1 = (e) => {
    this.setState({ display: "none" });
  };

  handlelogout = () => {
    fetch("/user/logout").then((data) => this.props.handlelogout(data)).catch(err=>{console.log(err)});
  };

  handleSidebar=()=>{
   this.setState({
     width:"200px",
     display:"block"
   })
  }

closeSidebar=()=>{
  this.setState({
    width:"0px",
    display:"none"
  })
}

insideSidebar = (props)=> { 
  const style={ 
                display:this.state.display,
                marginLeft: "20px",
                marginTop: "20px",
                borderBottom: "2px solid #eee",
                color: "#eee",
                fontWeight: "lighter",
              };
//Styles with the class names are not working properly. I don't know why?

  if(props.name==="Close" || props.name==="Logout"){
    return(<div style={style}  onClick={props.click}>{props.name}</div>)
  }else{
    return(
          <Link style={style}  to={props.linkto}>{props.name}</Link>
      )
  }
}

  render() {
    if (this.props.isauthenticated) {
      return (
        <div> 
        <div className='navbar'>
          <div className='navbar_menu_icon' onClick={this.handleSidebar} >
                <img  src={menu_icon}/>
          </div>

          <div className="sidebar" style={{width:this.state.width}}>      
             <this.insideSidebar name="Close" click={this.closeSidebar}/>
             <this.insideSidebar name="Hi Sara"  linkto="/mypost"/>
             <this.insideSidebar name="Home" linkto="/home"/>
             <this.insideSidebar name="Create Post"  linkto="/createpost"/>
             <this.insideSidebar name="My Post" linkto="/mypost"/>
             <this.insideSidebar name="All Post" linkto="/allpost"/>
             <this.insideSidebar name="Important Post" linkto="/importantpost"/>
             <this.insideSidebar name="Logout" click={this.handlelogout}/> 
          </div>

          <div className='navbar_left'>
            <div className='navbar_home'>
              <Link to='/home'> Home</Link>
            </div>

            

          <div className='navbar_posts'>   
            <div className='navbar_post'>
                <Link
                  to='/posts'
                  onMouseOver={this.handlehover}
                  onMouseOut={this.handlehover1}
                  name='posts'>
                  Posts
                </Link>
            </div>
              <div className='navbar_mypost'>
                <Link
                  to='/mypost'
                  name='mypost'
                  onMouseOver={this.handlehover}
                  onMouseOut={this.handlehover1}
                  style={{
                    display: this.state.display
                  }}>
                  MyPost
                </Link>
              </div>

              <div className='navbar_importantpost'>
                <Link
                  to='/importantpost'
                  name='importantpost'
                  onMouseOver={this.handlehover}
                  onMouseOut={this.handlehover1}
                  style={{ display: this.state.display }}>
                  Important Post
                </Link>
              </div>
              
              <div className='navbar_allpost'>
                <Link
                  to='/allpost'
                  name='allposts'
                  onMouseOver={this.handlehover}
                  onMouseOut={this.handlehover1}
                  style={{ display: this.state.display }}>
                  All Post
                </Link>
              </div>
            
            </div>
            <div className='navbar_create_post'>
              <Link to='/createpost'> Create Post</Link>
            </div>
          </div>

          
          <div className='navbar_ul_right'>
            <div className='navbar_username'>
              <Link to='/mypost'> Hi {this.props.name}</Link>
            </div>

            <div className='navbar_logout'>
              <Link onClick={this.handlelogout}> Logout</Link>
            </div>
          </div>
        </div>
        </div>
      );
    } else {
      return null;
    }
  }
}

export default Navbar;
