import React, { Component } from "react";
import "./editpost.css";

class CreatePost extends Component {
  state = {
    Heading: this.props.post_details.Heading,
    Post_Content: this.props.post_details.Post_Content,
    Author: this.props.id,
    Post_id: this.props.post_details.Post_id,
    success_message:""
  };

  handlesubmit = (e) => {
    e.preventDefault();
    fetch("/create/editpost", {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        Heading: this.state.Heading,
        Author: this.state.Author,
        Post_Content: this.state.Post_Content,
        Post_id: this.state.Post_id,
      }),
    }).then((data) => {console.log(data.status)
      if (data.status ===200){
        this.setState({
          Heading: "Enter Heading of the Post",
          Post_Content: "Type Your Post Here",
          success_message:"Post Successfully Submitted",
        })
        
      }}).catch(err=>{console.log(err)});
  };

  handlechange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  handleEdit = () => {
    if ((this.props.post_id = "newpost")) {
      return null;
    } else {
      this.setState({});
    }
  };

  render() {
    return (
          <form className='createpost' onSubmit={this.handlesubmit}>
            <div>
              <div className="createpost_heading">
                <input
                  type='text'
                  name='Heading'
                  value={this.state.Heading}
                  onChange={this.handlechange}
                />
              </div>
              <div >
                <textarea
                  name='Post_Content'
                  value={this.state.Post_Content}
                  onChange={this.handlechange}
                />
              </div>
            </div>
            <div>
              <button>Submit</button>
            </div>
          </form>
    );
  }
}

export default CreatePost;
