import React, { Component } from "react";
import { Link, Route, Redirect, BrowserRouter } from "react-router-dom";
import "./login.css";

class Login extends Component {
  state = {
    username: "",
    password: "",
    message:"",
  };

  handlelogin = (e) => {
    e.preventDefault();
    fetch("/user/login", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        username: this.state.username,
        password: this.state.password,
      }),
      credentials: "same-origin",
    })
      .then((res) => res.json())
      .then((data) => {
        this.setState({message:data.message})
        this.props.handlelogin(data);
      }).catch(err=>{console.log(err)});
  };

  handlechange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
      message:"",
    });
  };

  render() {
    if (!this.props.isauthenticated) {
      return (
        <div className='login'>
          <div className="login_navbar" >
            <div className="login_logo">
            <Link to='/'> Mugu</Link>            
            </div>
            <div className="login_login_register">
              <Link to='/login'> Login</Link>
              <Link to='/register'> Register</Link>
            </div>  
          </div>
          
          <h2 className='login_text'> Login</h2>
          <p style={{color:"red"}}>{this.state.message}</p>
          <form className='login_form' onSubmit={this.handlelogin}>
            <input
              className='login_username'
              type='text'
              name='username'
              placeholder='Username'
              onChange={this.handlechange}
            />

            <input
              className='login_password'
              type='text'
              name='password'
              placeholder='Password'
              onChange={this.handlechange}
            />
            <button className="login_submit"> Submit </button>
          </form>

          <div className='Not_registered'>
            <div>
              <Link to='/register'>Click Here</Link>If not Registered 
            </div>
          </div>
        </div>
      );
    } else {
      return <Redirect to='/home' />;
    }
  }
}

export default Login;
