import React, { Component } from "react";
import Navbar from "./Components/Navbar/Navbar.js";
import MyPost from "./Components/MyPost/Mypost";
import Allpost from "./Components/AllPost/Allpost";
import Importantpost from "./Components/ImportantPost/Importantpost";
import { BrowserRouter, Route, Link, Redirect, Switch } from "react-router-dom";
import Post from "./Components/Post/post";
import Login from "./Components/Login/login";
import Register from "./Components/Register/register";
import CreatePost from "./Components/CreatePost/Createpost.js";
import Landing from "./Components/LandingPage/landing";
import EditPost from "./Components/EditPost/EditPost";
import Home from "./Components/Home/home";

class App extends Component {
  state = {
    isauthenticated: false,
    id: " ",
    Name: " ",
    NumOfFollowers: "",
    Ifollow: "",
    MyPosts: Array,
    AllPosts: Array,
    ImportantPosts: Array,
  };

  handlelogin = (data) => {
    if (data._id) {
      this.setState({ isauthenticated: true, Name: data.name, id: data._id });
    }
  };

  handlelogout = (data) => {
    if (data.status === 200) {
      this.setState({ isauthenticated: false, Name: "" });
    }
  };

  PrivateRoute = ({ component: Component, ...rest }) => {
    return (
      <Route
        {...rest}
        render={(props) => {
          if (this.state.isauthenticated) {
            if (props.location.pathname === "/mypost") {
              return <MyPost id={this.state.id} />;
            } else if (props.location.pathname === "/importantpost") {
              return <Importantpost id={this.state.id}/>;
            } else if (props.location.pathname === "/allpost") {
              return <Allpost id={this.state.id}/>;
            } else if (props.location.pathname === "/home") {
              return <Home username={this.state.Name} />;
            } else if (props.match.path === "/createpost") {
              return <CreatePost id={this.state.id} />;
            } else if (props.match.path === "/post/:id") {
              return <Post post_id={props.match.params} />;
            } else if (props.match.path === "/login") {
              return <Redirect to='/home' />;
            } else if (props.match.path === "/editpost") {
              return (
                <EditPost
                  post_details={props.location.state}
                  id={this.state.id}
                />
              );
            }
          } else if (!this.state.isauthenticated) {
            if (props.location.pathname === "/login") {
              return (
                <Login
                  handlelogin={this.handlelogin}
                  isauthenticated={this.state.isauthenticated}
                />
              );
            } else if (props.location.pathname === "/register") {
              return <Register />;
            } else if (props.location.pathname === "/home") {
              return <Redirect to='/' />;
            } else if (props.location.pathname === "/mypost") {
              return <Redirect to='/' />;
            } else if (props.location.pathname === "/createpost") {
              return <Redirect to='/' />;
            } else if (props.location.pathname === "/allpost") {
              return <Redirect to='/' />;
            } else if (props.location.pathname === "/importantpost") {
              return <Redirect to='/' />;
            } else if (props.match.path === "/") {
              return <Landing isauthenticated={this.state.isauthenticated} />;
            }
          }
        }}
      />
    );
  };

  render() {
    return (
      <div>
        <BrowserRouter>
          <Navbar
            isauthenticated={this.state.isauthenticated}
            handlelogout={this.handlelogout}
            name={this.state.Name}
          />

          <this.PrivateRoute
            x='ankit'
            id={this.state.id}
            path='/mypost'
            component={MyPost}
          />

          <this.PrivateRoute
            username={this.state.Name}
            path='/home'
            component={Home}
          />
          <this.PrivateRoute
            id={this.state.id}
            path='/allpost'
            component={Allpost}
          />
          <this.PrivateRoute
            id={this.state.id}
            path='/importantpost'
            component={Importantpost}
          />
          <this.PrivateRoute x={this.props} path='/post/:id' component={Post} />
          <this.PrivateRoute
            id={this.state.id}
            path='/createpost'
            component={CreatePost}
          />
          <this.PrivateRoute
            id={this.state.id}
            path='/editpost'
            component={EditPost}
          />

          <this.PrivateRoute
            handlelogin={this.handlelogin}
            isauthenticated={this.state.isauthenticated}
            path='/login'
            component={Login}
          />
          <this.PrivateRoute path='/register' component={Register} />
          <this.PrivateRoute
            isauthenticated={this.state.isauthenticated}
            exact
            path='/'
            component={Landing}
          />
        </BrowserRouter>
      </div>
    );
  }
}

export default App;
