import React, { Component } from "react";
import { BrowserRouter, Route, Link, Redirect, Switch } from "react-router-dom";

import "./landing.css";
import landing_pic from "./Icons_and_Images/landing_pic.jpg";

class Landing extends Component {
  render() {
    if (this.props.isauthenticated) {
      return null;
    } else {
      return (
        <div className='landing_container'>

          <div className="landing_navbar" >
            <div className="landing_logo">
            <Link to='/'> Mugu</Link>            
            </div>
            <div className="landing_login_register">
              <Link to='/login'> Login</Link>
              <Link to='/register'> Register</Link>
            </div>  
          </div>

          <div className="landing_body">
            <div>
            <h2>Every idea needs a Medium</h2>
            <p>
            Anyone can write on Mugu. Thought-leaders, journalists, experts, 
            and individuals with unique perspectives share their thinking here. 
            You’ll find pieces by independent writers from around the globe, 
            stories we feature and leading authors, and smart takes on our own 
            suite of blogs and publications.
            </p>
            </div>
            
          <div>
            <img src={landing_pic} alt="logo"/>
          </div>
          </div>

        </div>
      );
    }
  }
}

export default Landing;
